<template>
  <div class="login" :class="{ login__signin: isSignIn, login__signup: isSignUp }">
    <LoginTabs @click.native="switchState" />
    <LoginForm />
  </div>
</template>

<script>
import LoginTabs from '../components/LoginTabs'
import LoginForm from '../components/LoginForm'

export default {
  name: 'login',
  components: { LoginTabs, LoginForm },
  data() {
    return {
      state: 1
    }
  },
  computed: {
    isSignIn() {
      return this.state === 1
    },
    isSignUp() {
      return this.state === 2
    }
  },
  methods: {
    switchState() {
      this.state = this.state === 1 ? 2 : 1
    }
  }
}
</script>

<style lang="less">
.login {
  width: 100%;
  color: white;
  font-size: 14px;
}
</style>
